<template>
	<div id="trans">
		<span class="title">{{$t('navBar.transactionFee')}}</span>
		<el-col :span="24" style="padding-bottom: 0px" class="toolbar">
	        <el-form :inline="true" :model="form">
				<el-col :span = "24" style="padding-bottom: 0px" class="toolbar">
					<el-form-item :label="$t('transactionFeeBuy')" >
				    	<el-input v-model="form.buyTradeFee" :placeholder="$t('PleaseService')"><template slot="append">% / 1BTY</template></el-input> 
					</el-form-item>
				</el-col>
				<el-col :span = "24" style="padding-bottom: 0px" class="toolbar">	
					<el-form-item :label="$t('transactionFeeSell')" >
				    	<el-input v-model="form.sellTradeFee" :placeholder="$t('PleaseService')"><template slot="append">% / 1BTY</template></el-input>
					</el-form-item>
				</el-col>
				<el-form-item>
				    <el-button type="warning"@click="setBTYUSDGTradeFee">{{$t('confirm')}}</el-button>
				</el-form-item>
	        </el-form>
	    </el-col> 
	</div>
</template>
<script>
import {ajax} from '../../common/js/common.js'
	export default{
		data(){
			return {
				form:{
					buyTradeFee:"",
					sellTradeFee:""
				},
			}

		},
		methods:{
			showBTYUSDGTradeFee:function(){
				ajax(this,this.ApiConfig.feeConfigure.showBTYUSDGTradeFee,{},(res) =>{
					console.log(res)
					if(res.code == 200){
						this.form.buyTradeFee = res.data.buyTrade;
						this.form.sellTradeFee = res.data.sellTrade;
					}else{
						this.$notify({
							title:"提示",
							message:res.message,
							type:'warning'
						})
						return false;
					}
				},"POST")
			},
			setBTYUSDGTradeFee:function(){
				ajax(this,this.ApiConfig.feeConfigure.setBTYUSDGTradeFee,this.form,(res) =>{
					console.log(res)
					if(res.code == 200){
						this.$notify({
							title:"提示",
							message:res.message,
							type:'success'
						})
					}else{
						this.$notify({
							title:"提示",
							message:res.message,
							type:'warning'
						})
						return false;
					}
				},"POST")
			}
			
		},
		mounted(){
			this.showBTYUSDGTradeFee();
		}
	}
</script>
<style>
	#trans{
		
		.el-button{
			/* width: 120px;
			height: 40px; */
			background-color: #bf9764;
			/* font-size: 16px; 
			border-radius: 6px;
			border: none;
			outline: none; */
		}
		.el-form-item__label{
			font-size: 14px;
    		color: #000;
		}

		.el-input__inner{
			font-size: 14px;
			width: 130px;
			}
		
			/* .cny{
				position: absolute;
				font-size: 14px;
				color: #a0a0a0;
				z-index: 999;
				left: 150px;
				top: 0px;
			} */

			.el-input-group__append, .el-input-group__prepend{
				background-color: transparent;
			}
}
</style>


